<template>
   <div ref="wrapper" class="scroll-wrap">  
    <div>  
        <slot></slot>  
    </div>  
  </div>  
   
</template>
<script>
    import BScroll from 'better-scroll'  
export default {  
  props:{  
    probeType:{  
      type:Number,  
      default:1  
    },  
    /**  
     * click  
     */  
    click:{  
      type:Boolean,  
      default:true  
    },  
    /**  
     *   
     */  
    scrollX:{  
      type:Boolean,  
      default:true  
    },  
    /**  
     *   
     */  
    listenScroll:{  
      type:Boolean,  
      default:false  
    },  
    /**  
     *   
     */  
    data:{  
      type:[Object,Array,String],  
      default:null  
    },  
    /**  
     *   
     */  
    pullup:{  
      type:Boolean,  
      default:false  
    },  
    /**  
     *   
     */  
    pulldown:{  
      type:Boolean,  
      default:false  
    },  
    /**  
     *   
     */  
    deforeScroll:{  
      type:Boolean,  
      default:false  
    },  
    /**  
     * ,scroll  
     */  
    refreshDelay:{  
      type:Number,  
      default:20  
    },
    scrollbar:{
      type:Boolean,  
      default:false  
    }  
  },  
  data(){  
    return{  
      dragTip:{  
        text:"",  
        translate:-50,  
        showLoding:false  
      },  
      isLoading: false,  
      isDone: false,  
    }  
  },  
  mounted(){  
    setTimeout(() => {  
      this._initScroll()  
    },20)  
  },  
  methods:{  
    _initScroll(){  
      if(!this.$refs.wrapper){  
        return  
      }  
      // better-scroll  
      this.scroll = new BScroll(this.$refs.wrapper,{  
        probeType:this.probeType,  
        click:this.click,  
        scrollX:this.scrollX,
        scrollbar:this.scrollbar  
      })  
      //   
      if(this.listenScroll){  
        let me = this  
        this.scroll.on('scroll',(pos) => {  
          if(this.listenScroll){  
            me.$emit('scroll',pos)  
          }  
        })  
      }  
    
      //   
      if(this.beforeScroll){  
        this.scroll.on('beforeScrollStart',() => {  
          this.$emit('beforeScroll')  
        })  
      }  
    },  
    disable(){  
      this.scroll && this.scroll.disable()    
    },  
    enable(){  
      this.scroll && this.scroll.enable()  
    },  
    refresh(){  
      this.scroll && this.scroll.refresh()  
    },  
    scrollTo(){  
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)  
    }, 
     
    scrollToElement() {  
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)   
    }  
  },  
  watch:{  
    data(){  
      setTimeout(() => {  
        this.refresh()  
        
      },this.refreshDelay)  
    }  
  }  
}  
</script>
<style  lang="scss" rel="stylesheet/scss" scoped >
 .scroll-wrap{
    height:100%;  
    overflow:hidden; 
    /*  */  
    .pulldown,.pullup{
        width:100%;  
        height:0.5rem;
        position:relative;
         div.clear{
            font-size:.14rem;  
            position:absolute;
            left:50%;
            top:5px;
            transform:translate(-50%,0);
        }
        .list-donetip {
        text-align:center;  
        line-height:0.5rem ; 
        font-size:.14rem ; 
      }   
    }  
    
 }  
    

      
   
</style>


